import React, { useState } from 'react';
import {
  AppBar, Toolbar, Typography, Button, IconButton, Box,
  Drawer, List, ListItem, ListItemButton, ListItemText, Divider,
  useMediaQuery, useTheme, Link as MuiLink // Alias Link to avoid conflict with react-router-dom Link if you add it later
} from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart'; // For the cart icon
import HomeIcon from '@mui/icons-material/Home'; // For Home icon
import InfoIcon from '@mui/icons-material/Info'; // For About icon
import QuestionAnswerIcon from '@mui/icons-material/QuestionAnswer'; // For FAQs icon
import ContactMailIcon from '@mui/icons-material/ContactMail'; // For Contact icon

function HeaderNavbar() {
  const [mobileOpen, setMobileOpen] = useState(false);
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('md')); // true if screen is md or smaller

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const navItems = [
    { name: 'Home', path: '#home-section', icon: <HomeIcon /> },
    { name: 'FAQs', path: '#faqs-section', icon: <QuestionAnswerIcon /> },
    { name: 'Contact', path: '#contact-us-section', icon: <ContactMailIcon /> },
    { name: 'About', path: '#about-section', icon: <InfoIcon /> }, // Added an example 'About'
    // Add more navigation items here if needed
  ];

  const drawer = (
    <Box onClick={handleDrawerToggle} sx={{ textAlign: 'center' }}>
      <Typography variant="h6" sx={{ my: 2, color: theme.palette.primary.main }}>
        Farren Embroidery
      </Typography>
      <Divider />
      <List>
        {navItems.map((item) => (
          <ListItem key={item.name} disablePadding>
            <ListItemButton
              sx={{ textAlign: 'left', px: 3 }}
              component={MuiLink} // Use MuiLink for internal scrolling
              href={item.path}
              onClick={(e) => {
                // Smooth scroll logic for hash links
                const targetElement = document.querySelector(item.path);
                if (targetElement) {
                  e.preventDefault();
                  targetElement.scrollIntoView({ behavior: 'smooth' });
                }
              }}
            >
              <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                {React.cloneElement(item.icon, { sx: { color: theme.palette.primary.main } })}
                <ListItemText primary={item.name} sx={{ color: theme.palette.whiteSectionText }} />
              </Box>
            </ListItemButton>
          </ListItem>
        ))}
        {/* Cart in mobile menu */}
        <ListItem disablePadding>
          <ListItemButton sx={{ textAlign: 'left', px: 3 }}>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
              <ShoppingCartIcon sx={{ color: theme.palette.primary.main }} />
              <ListItemText primary="Cart" sx={{ color: theme.palette.whiteSectionText }} />
            </Box>
          </ListItemButton>
        </ListItem>
      </List>
    </Box>
  );

  return (
    <AppBar component="nav" position="sticky" sx={{ bgcolor: theme.palette.background.default, boxShadow: '0 2px 4px rgba(0,0,0,0.2)', py: 1 }}>
      <Toolbar sx={{ justifyContent: 'space-between' }}>
        {/* Business Name on the Left */}
        <Typography
          variant="h4"
          component="div"
          sx={{ flexGrow: 1, color: theme.palette.secondary.main, fontWeight: 900, textShadow: '1px 1px 2px rgba(0,0,0,0.5)' }}
        >
          Farren Embroidery
        </Typography>

        {/* Mobile Hamburger Menu */}
        {isMobile ? (
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="end"
            onClick={handleDrawerToggle}
            sx={{ ml: 2, color: theme.palette.secondary.main }} // Yellow color for hamburger
          >
            <MenuIcon sx={{ fontSize: '2rem' }} />
          </IconButton>
        ) : (
          /* Desktop Navigation on the Right */
          <Box sx={{ display: 'flex', alignItems: 'center' }}>
            {navItems.map((item) => (
              <Button
                key={item.name}
                sx={{
                  color: 'white',
                  textTransform: 'none',
                  fontSize: '1.05rem',
                  mx: 1,
                  '&:hover': {
                    bgcolor: 'rgba(255,255,255,0.1)',
                    color: theme.palette.secondary.main,
                  }
                }}
                component={MuiLink} // Use MuiLink for internal scrolling
                href={item.path}
                onClick={(e) => {
                    const targetElement = document.querySelector(item.path);
                    if (targetElement) {
                        e.preventDefault();
                        targetElement.scrollIntoView({ behavior: 'smooth' });
                    }
                }}
              >
                {item.name}
              </Button>
            ))}
            {/* Cart Icon for Desktop */}
            <IconButton color="inherit" sx={{ ml: 2, color: theme.palette.secondary.main }}>
              <ShoppingCartIcon sx={{ fontSize: '1.8rem' }} />
            </IconButton>
          </Box>
        )}
      </Toolbar>
      <nav>
        <Drawer
          variant="temporary"
          open={mobileOpen}
          onClose={handleDrawerToggle}
          ModalProps={{
            keepMounted: true, // Better open performance on mobile.
          }}
          sx={{
            display: { xs: 'block', md: 'none' },
            '& .MuiDrawer-paper': {
              boxSizing: 'border-box',
              width: 240,
              bgcolor: theme.palette.background.paper, // Dark background for drawer
              color: theme.palette.text.primary, // Light text for drawer
            },
          }}
        >
          {drawer}
        </Drawer>
      </nav>
    </AppBar>
  );
}

export default HeaderNavbar;